/*
Accordion
*/
.accordion {
  background: #4d5158;
  box-shadow: 0 4px 40px #4d5158;
  margin: 4em auto; }
  .accordion li {
    padding: 24px; }
  .accordion ul > li {
    box-shadow: inset 0 -1px #52565d;
    color: #fff;
    transition: all 0.3s cubic-bezier(0, 0, 0.2, 1); }
    .accordion ul > li:hover, .accordion ul > li:focus {
      background: #52565d; }
    .accordion ul > li.has-child.active > .expander:before {
      transform: rotate(0deg); }
    .accordion ul > li.has-child ul > li {
      background: rgba(40, 42, 45, 0.6); }
    .accordion ul > li span.expander {
      cursor: pointer;
      display: inline-block;
      height: 25px;
      position: relative;
      vertical-align: middle;
      width: 25px;
      margin-right: 15px; }
      .accordion ul > li span.expander:before {
        color: #fff;
        content: '\E001';
        line-height: 1.4;
        position: absolute;
        transform: rotate(-90deg);
        transition: all 0.3s cubic-bezier(0, 0, 0.2, 1);
        font-family: "icons";
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        font-style: normal;
        font-variant: normal;
        font-weight: bold;
        text-decoration: none;
        text-transform: none; }
  .accordion ul ul {
    display: none;
    margin-top: 24px; }
